.app-sidebar {
	.side-menu li a.active {
		background: none;
	}
	.slide-menu li.active>a {
		font-weight: 500;
	}
}

.slide-menu li {
	position: relative;
	&.active {
		&:before {
			content: '';
			position: absolute;
			width: 15px;
			height: 2px;
			background: #e8ebed;
			left: -20px;
			top: 15px;
			display: block;
		}
		&:after {
			content: '';
			position: absolute;
			width: 8px;
			height: 8px;
			left: -10px;
			top: 12px;
			display: block;
			border-radius: 50%;
		}
	}
	ul {
		li {
			font-size: 12px !important;
			font-weight: 300;
			border-bottom: 0px solid rgba(0, 0, 0, 0.03);
		}
		a {
			padding: 10px 10px 10px 65px;
		}
	}
}

.side-menu .slide-menu li a {
	font-size: 13px;
	font-weight: 400;
}

.slide ul li a {
	color: #153039;
	font-weight: 400;
}

.app-sidebar-footer {
